import React, {Component} from 'react';
import {humanReadableByteCount} from "../../../../helper/helper";
import {Button} from 'reactstrap';
import 'react-datetime/css/react-datetime.css'
import Dropzone from 'react-dropzone'
import Notify from 'react-notification-alert';
import Table from "../../../../components/Table/Table";
import Alert from "react-s-alert";

class UploadEmployeeFile extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: [],
            acceptedFiles: [],
            loading: false,
            uploading: false,
            alert: null,
        };
        this.startUpload = this.startUpload.bind(this);
        this.canUpload = this.canUpload.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.removeAcceptedFile = this.removeAcceptedFile.bind(this);
    }

    startUpload() {

        let showError = false, errorMessage = "";
        if (!this.state.acceptedFiles || this.state.acceptedFiles.length === 0) {

            errorMessage = 'Yüklenecek dosyayı seçiniz';
            showError = true;
        }

        if (showError) {
            this.refs.notify.notificationAlert({
                place: 'tr',
                message: errorMessage,
                type: 'danger',
                autoDismiss: 5,
            });
            return;
        }

        this.props.onFileSelect(this.state.acceptedFiles[0]);
    }

    onDrop(acceptedFiles, rejectedFiles) {

        this.setState({acceptedFiles})

        if (rejectedFiles.length > 0) {
            this.refs.notify.notificationAlert({
                place: 'tr',
                message: 'Yalnızca \'xls\' veya \'xlsx\' uzantılı dosya seçebilirsiniz',
                type: 'danger',
                autoDismiss: 5,
            });
        }
    }

    removeAcceptedFile(row, index) {
        let {acceptedFiles} = this.state

        acceptedFiles.splice(index, 1);

        this.setState({acceptedFiles})
    }

    canUpload() {
        return (!!this.state.acceptedFiles && this.state.acceptedFiles.length > 0)
    }

    render() {
        return (
            <div>
                <Notify ref="notify"/>
                {this.state.alert}
                <div className="row">
                    <Dropzone
                        accept=".xls,.xlsx,.XLS,.XLSX"
                        className="FormRenk col-sm-12"
                        activeClassName="dropzoneActiveClass"
                        style={{}}
                        multiple={false}
                        onDrop={this.onDrop}>
                        <div className="dropzoneInnerArea">
                            {this.props.title ? this.props.title : "Toplu Çalışan Excel dosyalarınızı buraya sürükleyin veya seçmek için tıklayın"}
                        </div>
                    </Dropzone>
                </div>
                {this.state.acceptedFiles.length > 0 &&
                this.filesToBeUploaded()
                }
            </div>
        );
    }

    handleButtonClick = () => {
        if (this.state.acceptedFiles.some(file => file.size > 75 * 1024 * 1024)) {
            Alert.error("Yüklenen dosya boyutu 75MB' tan küçük olmalıdır", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }

        this.startUpload();
    }


    filesToBeUploaded() {

        const columns = [
            {
                Header: "Dosya Adı",
                accessor: "name",
                sortable: false
            },
            {
                Header: "Boyut",
                accessor: "size",
                sortable: false,
                Cell: row => (<div>{humanReadableByteCount(row.size, true)}</div>)
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: (row, index) => (
                    <div>
                        <button type="button"
                                onClick={() => this.removeAcceptedFile(row, index)}
                                data-toggle="tooltip" data-placement="bottom" title="Sil"
                                style={{background: 'crimson'}} rel="tooltip"
                                className="Btn btn-icon">
                            <i className="BtnIcon fa fa-remove "/>
                        </button>
                    </div>
                )
            }
        ];
        return (
            <div className="row">
                <h4 style={{width: "100%"}}>Yüklenecek Dosyalar</h4>
                <Button color="primary" onClick={this.handleButtonClick} disabled={this.state.uploading || !this.canUpload()}>
                    <span>Yüklemeyi Başlat&nbsp;&nbsp;</span>
                    <i className="fa fa-upload"/>
                </Button>
                <Table columns={columns}
                       content={this.state.acceptedFiles}
                       paginationEnabled={false}
                />
            </div>
        );
    }
}

export default UploadEmployeeFile;