import React, {Component} from 'react';
import 'react-table/react-table.css'
import Table from "../../../../components/Table/Table";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import {authorizationWays, showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import {
    addCustomFields,
    genderData,
    getJobTitles,
    renewListForDepartment,
    statusTypeFilterData
} from "../../common/PeykHelper";
import DefaultModal from "../../../../components/DefaultModal";
import Alert from "react-s-alert";
import DropdownCheck from "../../../../components/Table/DropdownCheck";
import DepartmentSelectionTree from "../../organization/DepartmentSelectionTree";
import Joyride from "react-joyride";
import {Col, Input} from "reactstrap";
import * as XLSX from 'xlsx';
import {isValidTckn} from "../../../../helper/TCKimlikNoValidator";
import UploadEmployeeFile from "../../employee/component/UploadEmployeeFile";
import SweetAlert from "react-bootstrap-sweetalert";
import Row from "react-bootstrap/Row";

class AddEmployeeModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selection: [],
            selectedAll: false,
            conditionalSelection: [],
            declineSelection: [],
            conditionalSelectedAll: false,
            declineSelectedAll: false,
            filtered: [],
            filterAll: true,
            loading: false,
            manual: false,
            content: [],
            treeData: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            titleFilterList: [],
            showDetailedSearch: false,
            searchKey: "",
            steps: [

                {
                    target: '.card-title',
                    content: "Çalışan ekleme işlemlerini bu ekrandan yapabilirsiniz",
                    placement: "center",
                },
                {
                    target: 'table thead tr:nth-child(1) th:nth-child(1) div',            //table tbody tr:nth-child(1) td:nth-child(1) input
                    content: "Ekleyeceğiniz çalışanları seçiniz",
                    placement: "auto",
                },
                {
                    target: 'table thead tr:nth-child(1) th:nth-child(8) div',             //table tbody tr:nth-child(1) td:nth-child(7) input
                    content: 'Şartlı onay gerekiyorsa işaretleyiniz',
                    placement: "auto",
                },
                {
                    target: '.kaydet',
                    content: 'İşlemleri tamamladıktan sonra tıklayınız',
                    placement: "auto",
                }
            ],
            locale: {
                back: 'Geri',
                close: 'Kapat',
                last: 'Son',
                next: 'İleri',
                skip: 'Atla',
            },
            joyrideActive: false,
            shouldRun: true,
            flag: false,
            detailedDepartment: false,
            showExcelImport: true,
            selectedAuthorizationWay: authorizationWays.DEPARTMENT
        };
        this.fetchData = this.fetchData.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.searchWithKey = this.searchWithKey.bind(this);
        this.setSelectedDepts = this.setSelectedDepts.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.isConditionalAllowed = this.isConditionalAllowed.bind(this);
        this.isDeclineAllowed = this.isDeclineAllowed.bind(this);
        this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
        this.toggleConditionalChange = this.toggleConditionalChange.bind(this);
        this.toggleDeclineChange = this.toggleDeclineChange.bind(this);
        this.onAuthorizationWaysChange = this.onAuthorizationWaysChange.bind(this);
    }

    componentDidMount() {
        this.setState({conditionalSelection: this.props.conditionalList, declineSelection: this.props.declineList, jobTitlesData: getJobTitles()});
        this.readDepartmentTreeData();
        this.readTitleFilterAll();
    }

    async onAuthorizationWaysChange(event) {
        let self = this;
        await self.setState({selectedAuthorizationWay: event.target.value});
        self.readDepartmentTreeData();
        self.handleSearch();
    }


    readTitleFilterAll = () => {
        let self = this;
        let params = {
            url: `/employeecustom/readTitleAllForFilter`,
            method: "get"
        };
        self.setState({showDetailedSearch: false});
        requestK(params)
            .then(function (response) {
                self.setState({titleFilterList: response.data, showDetailedSearch: true});
            })
            .catch(function () {
                self.setState({showDetailedSearch: true});
            });
    };


    readDepartmentTreeData = () => {
        let self = this;
        self.setState({treeData: [], selectedDepts: []});
        let params = {
            url: '/department/readDepartmentTreeData',
            method: "post",
            data: self.state.selectedAuthorizationWay
        };
        requestK(params)
            .then(function (response) {
                self.setState({treeData: response.data});
                self.props.reload(false);
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true,
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    searchWithKey(e) {
        this.setState({searchKey: e.target.value, manual: true});
    }

    setSelectedDepts(url) {
        let self = this;
        let selectedDepts = self.state.selectedDepts;
        if (selectedDepts) {
            Object.keys(selectedDepts).map(dept => {
                if (selectedDepts[dept]) {
                    if (self.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE) {
                        url = url + '&expenditureDepartment_id=' + dept;
                    } else {
                        url = url + '&department_id=' + dept;
                    }
                }
            });
        }
        return url;
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let searchQueryResult = self.createCustomFields(searchQuery);
        let url = '?page=' + page + '&size=' + size + searchQueryResult[0] + sortStr +
            '&customFields=' + searchQueryResult[1];
        url = self.setSelectedDepts(url);
        let params = {
            url: '/employee/readActiveEmployeeAll' + url,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                    searching: false
                });
            })
            .catch(function (error) {
                self.setState({loading: false, searching: false});
                showAxiosError(error);
            });
    }

    createCustomFields = (searchQuery) => {
        let customFieldsQuery = "";
        if (searchQuery.length) {
            let searchList = searchQuery.toString().split("&");
            searchQuery = "";
            searchList.map(e => {
                if (e.length) {
                    if (e.includes("cstmfld")) {
                        customFieldsQuery = customFieldsQuery + (customFieldsQuery.length ? "#" : "") + e.replace("cstmfld", "");
                    } else {
                        searchQuery = searchQuery + "&" + e;
                    }
                }
            });
        }
        return [searchQuery, customFieldsQuery];
    };

    saveListFromExcel = (list) => {
        let self = this;
        let params = {
            url: `/employee/getFormListByExcel`,
            method: "post",
            data: list
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                self.setState({selection: response.data, loading: false, manual: true});
                if (response.data.length) {
                    self.setState({showExcelImport: false});
                    self.setState({
                        alert: (
                            <SweetAlert info
                                        confirmBtnText="Tamam"
                                        confirmBtnBsStyle="success"
                                        title="Excel'den seçim yapıldı."
                                        onConfirm={() => {
                                            self.setState({alert: null});
                                            self.saveClicked();
                                        }}
                            >
                                <div>
                                    <label>{response.data.length + " adet seçim yapıldı."}</label>
                                    {(list.length - response.data.length > 0) && <label>
                                        <i className="fa fa-exclamation-triangle"/>
                                        &nbsp;&nbsp;{"Kayıtlı olmayan TCKN'ler vardır."}
                                    </label>}
                                </div>
                            </SweetAlert>
                        )
                    });
                } else {
                    self.setState({
                        alert: (
                            <SweetAlert warning
                                        confirmBtnText="Tamam"
                                        confirmBtnBsStyle="warning"
                                        title="Excel'den seçim yapılamadı."
                                        onConfirm={() => self.setState({alert: null})}
                            >
                                Çalışan listenizde uyuşan TCKN bulunmamaktadır.
                            </SweetAlert>
                        )
                    });
                }
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false, manual: true});
            });
    };

    bulkFileSelected = (fileContent) => {
        let data = [];
        let invalidData = [];
        let multiData = [];
        let workbook = XLSX.read(fileContent, {type: "array"});
        if (workbook.Directory && workbook.Directory.vba && workbook.Directory.vba.length) {
            Alert.error("Excel macro içermemelidir.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }
        let sheet = workbook.Sheets[workbook.SheetNames[0]];
        let range = XLSX.utils.decode_range(sheet["!ref"]);

        let isFirstRow = true;
        let hasTcknTitle = false;
        for (let row = range.s.r; row <= range.e.r; row++) {
            let validTcknFound = false;
            let multiTcknFound = false;
            let allRowEmpty = true;
            for (let col = range.s.c; col <= range.e.c; col++) {
                let cell = sheet[XLSX.utils.encode_cell({r: row, c: col})];
                let content = "";
                if (cell) {
                    allRowEmpty = false;
                    if (cell.t === "s") {
                        content = cell.v;
                    } else if (cell.t === "n") {
                        content = parseInt(cell.w).toString();
                    }
                }
                if (content && content.length > 0) {
                    if (isValidTckn(content)) {
                        data.push(content);
                        if (!validTcknFound) {
                            validTcknFound = true;
                        } else {
                            multiTcknFound = true;
                        }
                    } else if (isFirstRow && (content.toString().toLowerCase().includes("tc") || content.toString().toLowerCase().includes("t.c"))) {
                        hasTcknTitle = true;
                    }
                }
            }
            if (!(isFirstRow && hasTcknTitle) && !allRowEmpty) {
                if (!validTcknFound) {
                    let invalid = sheet[XLSX.utils.encode_cell({r: row, c: 0})].v;
                    if (range.e.c > 1) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 1})].v;
                    }
                    if (range.e.c > 2) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 2})].v;
                    }
                    if (!invalidData.length) {
                        invalidData.push("Satır: " + (row + 1) + " - " + invalid + "...");
                    } else {
                        invalidData.push("\r\n" + "Satır: " + (row + 1) + " - " + invalid + "...");
                    }
                } else if (multiTcknFound) {
                    let invalid = sheet[XLSX.utils.encode_cell({r: row, c: 0})].v;
                    if (range.e.c > 1) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 1})].v;
                    }
                    if (range.e.c > 2) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 2})].v;
                    }
                    if (!multiData.length) {
                        multiData.push("Satır: " + (row + 1) + " - " + invalid + "...");
                    } else {
                        multiData.push("\r\n" + "Satır: " + (row + 1) + " - " + invalid + "...");
                    }
                }
            }
            isFirstRow = false;
        }

        if (!invalidData.length && !multiData.length) {
            this.saveListFromExcel(data);
        } else {
            this.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="warning"
                                title="Yükleme Başarısız"
                                onConfirm={() => this.setState({alert: null})}
                    >
                        <div>
                            {!!invalidData.length &&
                                <label>{"Excel'de her satırda geçerli Tckn beklenmektedir. Buna uyumsuz satır bulunmaktadır. Aşağıda listelenmiştir."}</label>}
                            {!!invalidData.length &&
                                <Input type="textarea" rows={5}
                                       style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                                       value={invalidData}
                                       disable={true}/>}
                            {!!multiData.length &&
                                <label>{"Excel'de bir satırda bir tckn olmalıdır. Buna uyumsuz satır bulunmaktadır. Aşağıda listelenmiştir."}</label>}
                            {!!multiData.length &&
                                <Input type="textarea" rows={5}
                                       style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                                       value={multiData}
                                       disable={true}/>}
                        </div>
                    </SweetAlert>
                )
            });
        }
    };

    onFileSelect = (file) => {
        let fileReader = new FileReader();
        fileReader.readAsArrayBuffer(file);
        fileReader.onload = () => {
            this.bulkFileSelected(fileReader.result);
        };
    };


    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    toggleConditionalChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = [];
        self.state.content.map(emp => items.push(emp.id));
        let selection = self.state.conditionalSelection;
        if (row === "all") {
            selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item);
                });
            }
            selection = Array.from(new Set(selection));
            self.setState({conditionalSelection: selection, conditionalSelectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),

                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            selection = Array.from(new Set(selection));
            self.setState({
                conditionalSelection: selection,
                conditionalSelectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    toggleDeclineChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = [];
        self.state.content.map(emp => items.push(emp.id));
        let selection = self.state.declineSelection;
        if (row === "all") {
            selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item);
                });
            }
            selection = Array.from(new Set(selection));
            self.setState({declineSelection: selection, declineSelectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),

                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            selection = Array.from(new Set(selection));
            self.setState({
                declineSelection: selection,
                declineSelectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    isSelected(id) {
        return (
            (this.props.selectedList && this.props.selectedList.indexOf(id) !== -1) ||
            (this.state.selection && this.state.selection.indexOf(id) !== -1)
        );
    }

    isConditionalAllowed(id) {
        return (
            (this.state.conditionalSelection && this.state.conditionalSelection.indexOf(id) !== -1)
        );
    }

    isDeclineAllowed(id) {
        return (
            (this.state.declineSelection && this.state.declineSelection.indexOf(id) !== -1)
        );
    }

    getTrStyles(row) {
        return {
            backgroundColor: (
                (this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1) ? "#ffc" : "#f9f9f9"
            )
        };
    }

    saveClicked() {
        if (this.state.selection.length > 0) {
            this.props.completed([...this.props.selectedList, ...this.state.selection], this.state.conditionalSelection, this.state.declineSelection);
        } else {
            Alert.error("En az bir çalışan seçmelisiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 3000
            });
        }
    }

    activateJoyride() {
        if (this.state.joyrideActive === false) {
            this.setState({joyrideActive: true});
        }
    }

    callback = (data) => {
        if (data.action === 'close' && data.type === 'step:after') {
            this.setState({shouldRun: false});
        }
        if (data.action === 'stop' || data.action === 'reset') {
            this.setState({shouldRun: true});
            this.setState({joyrideActive: false});
            this.activateJoyride();
        }
    };

    detailedDepartment = () => {
        const b = document.getElementById('departmentContainer');
        this.setState({
            detailedDepartment: !this.state.detailedDepartment
        }, () => {
            if (this.state.detailedDepartment) {
                b.style.height = '600px';
            } else {
                b.style.height = '200px';
            }
        });
    };

    render() {
        let steps = this.state.steps;
        let locale = this.state.locale;

        let pageIdList = [], dublicateList = [], tempList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                if (!e.name || e.statusType === "ACTIVE") {
                    pageIdList.push(e.id);
                }
                if (!tempList.includes(e.tckn)) {
                    tempList.push(e.tckn);
                } else if (!dublicateList.includes(e.tckn)) {
                    dublicateList.push(e.tckn)
                }
            });
        }

        let columnList = [
            {
                Header: (
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => this.setState({selection: e})}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                Header: "Ad",
                accessor: "name",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={15}/></div>)
            },
            {
                Header: "Soyad",
                accessor: "surname",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.surname} maxLength={15}/></div>)
            },
            {
                Header: "Departman",
                accessor: "department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.department &&
                        <TextOverFlowTooltip text={row.department.name} maxLength={15}/>}</div>)
            },
            {
                Header: "Görev",
                accessor: "jobTitle",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.jobTitle} maxLength={15}/></div>)
            },
            {
                Header: "TCKN",
                accessor: "tckn"
            },
            {
                Header: "Masraf Yeri",
                accessor: "expenditureDepartment",
                Cell: row => (
                    <div style={{margin: 0}}>{row.expenditureDepartment &&
                        <TextOverFlowTooltip text={row.expenditureDepartment.name} maxLength={15}/>}</div>)
            },
            {
                Header: (this.state.content &&
                    <div>
                        <span style={{color: '#007acc'}}>Şartlı Onay</span>
                        <br/>
                        <DropdownCheck selectionIdList={this.state.conditionalSelection}
                                       totalIdList={this.state.totalIdList}
                                       pageIdList={pageIdList}
                                       onChange={e => this.setState({conditionalSelection: e})}/>
                    </div>),
                accessor: "conditional",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" style={{width:'56px'}} checked={this.isConditionalAllowed(row.id)}
                           disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                           onChange={(e) => this.toggleConditionalChange(e, row, "id", index)}/>)
            },
            {
                Header: (this.state.content && (this.props.noTemplate || this.props.decline === 1) &&
                    <div>
                        <span style={{color: '#408f57'}}>Reddedilebilir</span>
                        <br/>
                        <DropdownCheck selectionIdList={this.state.declineSelection}
                                       totalIdList={this.state.totalIdList}
                                       pageIdList={pageIdList}
                                       onChange={e => this.setState({declineSelection: e})}/>
                    </div>),
                accessor: "decline",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content && (this.props.noTemplate || this.props.decline === 1) &&
                    <input type="checkbox" style={{width:'75px'}} checked={this.isDeclineAllowed(row.id)}
                           disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                           onChange={(e) => this.toggleDeclineChange(e, row, "id", index)}/>)
            },
            {
                Header: "Durum",
                accessor: "statusType",
                Cell: row => (
                    <div>
                        <span>{statusTypeFilterData().map(function (e) {
                            if (e.value === row.statusType) return e.label
                        })}</span>
                    </div>)
            },
        ];
        let columns = renewListForDepartment(this, columnList, "accessor");

        const fieldList = [
            {
                label: "İsim",
                accessor: "name",
                type: "text",
                operator: "=",
            },
            {
                label: "TCKN",
                accessor: "tckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Soyisim",
                accessor: "surname",
                type: "text",
                operator: "=",
            },
            {
                label: "Departman",
                accessor: "department_name",
                type: "text",
                operator: "=",
            },
            {
                label: "Görev",
                accessor: "jobTitle",
                type: "select",
                operator: "=",
                selectData: this.state.jobTitlesData
            },
            {
                label: "Cinsiyet",
                accessor: "gender",
                type: "select",
                operator: "=",
                selectData: genderData()
            },
            {
                label: "İşe Başlama Tarihi (Başlangıç)",
                accessor: "min_employmentStartDate", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "İşe Başlama Tarihi (Bitiş)",
                accessor: "max_employmentStartDate",
                type: "date",
                operator: "<=",
            },
            {
                label: "Çalışma Şekli",
                accessor: "employmentStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "Adres",
                accessor: "address",
                type: "text",
                operator: "=",
            },
            {
                label: "Eğitim Durumu",
                accessor: "educationLevel",
                type: "text",
                operator: "=",
            },
            {
                label: "Askerlik Durumu",
                accessor: "militaryStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "Doğum Tarihi (Başlangıç)",
                accessor: "min_dateOfBirth", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "Doğum Tarihi (Bitiş)",
                accessor: "max_dateOfBirth",
                type: "date",
                operator: "<=",
            },
            {
                label: "Doğum Yeri",
                accessor: "cityOfBirth",
                type: "text",
                operator: "=",
            }
        ];
        addCustomFields(this.state.titleFilterList, fieldList);
        let fields = renewListForDepartment(this, fieldList, "accessor");

        return <DefaultModal disabled={this.state.btnDisabled}
                             isOpen={this.props.isOpen}
                             size="xl"
                             title="Çalışan Ekleme Ekranı"
                             secondaryButton={<button className="helpButton" onClick={() => {
                                 this.activateJoyride()
                             }} style={{position: "absolute", right: "70px", alignSelf: "center", marginTop: "-5px"}}>
                                 <span className="tooltipText">Size yardımcı olabilirim</span>
                                 <i className="far fa-question-circle helpButtonIcon" style={{opacity: ".5"}}/>
                             </button>}
                             confirmBtnText="Kaydet"
                             cancelBtnText="Kapat"
                             onConfirm={this.saveClicked}
                             onCancel={() => this.props.close()}>
            <div>
                {this.state.joyrideActive &&
                    <Joyride
                        locale={locale}
                        steps={steps}
                        continuous={true}
                        run={this.state.shouldRun}
                        scrollToFirstStep={true}
                        showProgress={true}
                        showSkipButton={true}
                        scrollToSteps={true}
                        disableCloseOnEsc={true}
                        disableOverlay={false}
                        disableOverlayClicks={false}
                        keyboardNavigation={true}
                        hideBackButton={false}
                        disableOverlayClose={true}
                        callback={this.callback}
                        holePadding={0}
                        autoStart
                        shouldRun={this.state.shouldRun}
                        styles={{
                            options: {
                                zIndex: 100000,
                                primaryColor: '#50a8e2'
                            }
                        }}
                    />
                }

                <div className="row"
                     style={{
                         paddingTop: "1%",
                         marginTop: "1%",
                         marginBottom: "1%",
                         paddingLeft: "1.8%",
                         paddingRight: "1.8%"
                     }}>
                    {this.state.treeData && this.state.treeData.length > 0 &&
                        <div className="col-xs-12 col-md-6" id='departmentContainer' style={{padding: 0}}
                             onClick={() => this.detailedDepartment()}>
                            <Col sm={12}>
                                <div style={{textAlign: 'center'}} onChange={this.onAuthorizationWaysChange}>
                                    <input sm={6} type="radio" value={authorizationWays.DEPARTMENT}
                                           name="authorizationWay"
                                           checked={this.state.selectedAuthorizationWay === authorizationWays.DEPARTMENT}/> Departman
                                    <input sm={6} type="radio" value={authorizationWays.EXPENDITURE}
                                           name="authorizationWay"
                                           checked={this.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE}
                                           style={{marginLeft: 20}}
                                    /> Masraf Yeri
                                </div>
                            </Col>
                            <Col id='departmentHeader' style={{padding: 0}}>
                                <label id='departmentHeaderContent'>Daha Fazla Göster
                                    <span style={{marginLeft: '5px', fontSize: '14px'}}><i
                                        className="fas fa-chevron-down"/></span>
                                </label>
                            </Col>
                            <DepartmentSelectionTree treeData={this.state.treeData}
                                                     selectedDepts={this.state.selectedDepts}
                                                     loading={e => this.setState({loading: e})}
                                                     onSelect={e => this.setState({selectedDepts: e, manual: true})}
                                                     selectedAuthorizationWay={this.state.selectedAuthorizationWay}
                                                     changeOnConditionalList={e => this.setState({conditionalSelection: e})}
                                                     changeOnDeclineList={e => this.setState({declineSelection: e})}/>
                        </div>}
                    {this.state.showExcelImport &&
                        <div className="col-xs-12 col-md-6">
                            <UploadEmployeeFile onFileSelect={this.onFileSelect}
                                                title={<div style={{textAlign: 'center'}}>
                                                    <Row>
                                                        <img
                                                            alt={"excel import"}
                                                            src={"data:image/jpeg;base64,"}
                                                            style={{height: '65px'}}/>
                                                    </Row>
                                                    <Row><span>Çalışan seçimini Excel'den yükleyerek yapmak için tıklayın</span></Row>
                                                </div>}/>
                        </div>}
                </div>
                {this.state.showDetailedSearch &&
                    <DetailedSearch fields={fields}
                                    loading={this.state.loading}
                                    onSearch={this.handleSearch}/>}
                <div className="col-xs-12" style={{marginTop: '20px'}}>
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           total={this.state.total}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50", "100"]}
                           fetchData={this.fetchData}
                           getTrStyles={this.getTrStyles}
                    />
                </div>
            </div>
            {this.state.alert}
        </DefaultModal>
    }

}

export default AddEmployeeModal;
